export const initialState = {
    basket: [],
    user: null
};


// Selector for totaling price
// it is just like for loop and talling the total price
export const getBasketTotal = (basket) =>
    basket?.reduce((amount, item) => item.price + amount, 0);

const reducer = (state,action) => {
    console.log(action);
    switch (action.type){
        case 'ADD_TO_BASKET':
            return {
                ...state,
                basket: [...state.basket, action.item]
            };

        case "REMOVE_FROM_BASKET":
            // if similar item are added then deleting using 
            // id will delete all the repeated item
            // solution::
            // use index
            const index = state.basket.findIndex(
                (basketItem) => basketItem.id === action.id
            );
            // store all the added item in new basket
            let newBasket = [...state.basket];
            
            // check if there are any item in basket
            if (index >= 0) {
                newBasket.splice(index, 1);
            } else {
                console.warn(
                    "Can't remove product (id: ${action.id}) as its not in basket!"
                )
            }

            return {
                ...state,
                basket: newBasket
            }
        
        case "SET_USER":
            return {
                ...state,
                user: action.user
            }

        default:
            return state;
    }
};

export default reducer;